import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const COfCodeDataStructures = () => (
    <Layout>
        <SEO title="C of Code Data Structures" />
        <Link to="/">Home </Link>
        <Link to="/cofcode/">/ C Of Code </Link>
        <h1>C Data Structures</h1>
        <p>Select an article</p>
        <p>
            <Link to="/coc_ds_array/">Arrays</Link>
        </p>
    </Layout>
)

export default COfCodeDataStructures
